.recommend-ul {
  margin-top: 3rem;
  padding: 0 .7rem;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  -ms-flex-direction: column;
      flex-direction: column;
}
.recommend-ul .recommend-li {
  margin: .5rem 0;
}
.recommend-ul .recommend-li .linear {
  margin-top: .9rem;
  height: 1px;
  background: #000;
  -webkit-transform: scaleY(0.5);
  -webkit-transform-origin: 0 0;
  -ms-transform: scaleY(0.5);
      transform: scaleY(0.5);
  -ms-transform-origin: 0 0;
      transform-origin: 0 0;
  overflow: hidden;
}
.show-more {
  width: 100%;
  height: 2rem;
  font-size: 12px;
  line-height: 2rem;
  text-align: center;
  opacity: .6;
}



