.commend {
  width: 100%;
  height: 100%;
}
.commend .title {
  font-weight: bold;
  font-size: 1rem;
  margin: .4rem 0;
}
.commend .loadmore a {
  display: block;
  width: 100%;
  height: 2rem;
  text-align: center;
  color: #b93321;
  margin-top: .6rem;
  font-size: .8rem;
  cursor: pointer;
}
.commend .shadowBar {
  width: 100%;
  height: .5rem;
  background: #f4f4f4;
  margin-bottom: 1rem;
}



