.ranking-section {
  width: 100%;
  height: 100%;
}
.ranking-section .ranking-head {
  width: 100%;
  height: 3rem;
  color: #999;
  font-size: .8rem;
  text-indent: .3rem;
  line-height: 3rem;
  background: #e6e6e6;
}
.ranking-section .ranking-detail {
  width: 100%;
  height: 100%;
  padding: 0 0 0 1rem;
}
.ranking-section .ranking-detail ul li {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  width: 100%;
  height: 3rem;
  -ms-flex-align: center;
      align-items: center;
}
.ranking-section .ranking-detail ul li img {
  width: 2rem;
  height: 2rem;
}
.ranking-section .ranking-detail ul li p {
  position: relative;
  margin-left: 1rem;
  height: 3rem;
  width: 100%;
  line-height: 3rem;
  font-size: .8rem;
  background-size: 100% 1px;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(50%, transparent), color-stop(0, #ebebeb));
  background-image: -webkit-linear-gradient(top, transparent 50%, #ebebeb 0);
  background-image: -o-linear-gradient(top, transparent 50%, #ebebeb 0);
  background-image: linear-gradient(180deg, transparent 50%, #ebebeb 0);
  background-repeat: no-repeat;
  background-position: bottom;
}
.ranking-section .ranking-detail ul li p .pushDown {
  position: absolute;
  top: 0rem;
  right: 1rem;
}
.ranking-section .ranking-detail ul li p .pushDown img {
  -webkit-transition: all 0.1s rese-in;
  -o-transition: all 0.1s rese-in;
  transition: all 0.1s rese-in;
  width: 1rem;
  height: 1rem;
}



