.search {
  display: -ms-flexbox;
  display: flex;
  height: 3rem;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-align: center;
      align-items: center;
}
.search div:nth-child(1) {
  width: 85%;
}
.search div:nth-child(1) label input {
  width: 100%;
  height: 2rem;
  border: 1px solid #999;
}
.search div:nth-child(2) {
  width: 15%;
  height: 1.9999rem;
  cursor: pointer;
}
.search div:nth-child(2) button {
  background: #c9b388;
  width: 100%;
  height: 2rem;
}
.search div:nth-child(2) button img {
  width: 2rem;
  height: 1.9999rem;
}



